.ProductDownloadableSamples {
    &-Wrapper {
        order: 16;
    }

    &-Title {
        font-weight: bold;
        margin-bottom: .5rem;

        @include mobile {
            display: none;
        }
    }

    &-Link {
        padding: .5rem 0;
    }


    &-ExpandableContent {
        &Button {
            @include after-mobile {
                display: none;
            }
        }

        &Content_isContentExpanded {
            @include mobile {
                padding: 0 1.4rem 1.4rem;
            }
        }
    }
}
